import React, { Component } from 'react'


export default class SignUpForm extends Component {
  render() {
    return (
      <form name="signUp" onSubmit={this.props.onSubmit.bind(this)}> {/* */}
        <input type="text" value={this.props.formData.username}
          onChange={this.props.onChange.bind(this, 'username')} placeholder="" />
        <input type="text" value={this.props.formData.email}
          onChange={this.props.onChange.bind(this, 'email')} placeholder="" />
        <input type="password" value={this.props.formData.password}
          onChange={this.props.onChange.bind(this, 'password')} placeholder="" />
        <input id="submit" type="submit" value="" />
      </form>
    )
  }

}


